/*
    choose style
 */

.choose {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #e7f0ff 100%);
  padding: 25px 0 70px;

  @include mq(md) {
    padding: 15px 0 45px;
  }

  p.send {
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      text-decoration: none;
    }
  }

  &__content {
    margin-top: 86px;
    position: relative;

    @include mq(md) {
      margin-top: 61px;
    }

    h2 {
      text-align: center;
      margin-left: auto;
      max-width: 559px;

      @include mq(md) {
        margin: 0 auto;
      }
    }

    h4 {
      line-height: 150%;
      text-align: center;
      max-width: 555px;
      margin: 26px 0 38px auto;

      @include mq(md) {
        margin: 7px auto 38px;
      }

      @include mq(s) {
        margin-bottom: 251px;
      }

      span {
        display: block;
        color: #ee4057;
      }
    }

    &-img {
      max-width: 457px;
      position: absolute;
      top: 23px;
      left: 0;

      @include mq(lg) {
        max-width: 350px;
        top: 53px;
      }

      @include mq(md) {
        position: relative;
        top: 0;
        left: 0;
        margin: 0 auto;
      }

      @include mq(s) {
        width: 100%;
        max-width: 400px;
      }

      figure {
        position: relative;
        z-index: 9;

        picture {
          display: block;
          width: 100%;
          border-radius: 20px;
          box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.25);
          overflow: hidden;

          img {
            width: 100%;
            position: relative;
          }
        }

        img {
          position: absolute;
          margin: 0;

          &.girl {
            width: 85%;
            max-width: 386px;
            position: absolute;
            bottom: 0;
            left: 6.56%;

            @include mq(s) {
              width: 86%;
              min-width: 250px;
              left: 50%;
              transform: translateX(-50%);
            }
          }

          &.product {
            width: 75%;
            max-width: 341px;
            bottom: calc(8.83% - 30px);
            right: calc(-49.23% - 20px);

            @include mq(s) {
              top: -15%;
              left: 50%;
              transform: translateX(-50%);
            }

            @include mq(xs) {
              width: 100%;
              top: -20%;
            }
          }

          &.smile {
            width: 43%;
            max-width: 194px;
            top: calc(26% - 10px);
            right: calc(-22.75% - 20px);

            @include mq(s) {
              width: 100%;
              top: -240px;
              left: 0;
            }
          }
        }

        .sale {
          position: absolute;
          bottom: calc(4.86% + 9px);
          right: calc(-19.74% + 3px);

          @include mq(s) {
            top: -200px;
            right: 10px;
          }

          @include mq(xs) {
            width: 94px;
            height: 47px;
            top: -176px;
          }

          span {

            @include mq(xs) {
              font-size: 24px;
              top: 7.83px;
              left: 19.52px;
            }
          }
        }
      }

      &:before, &:after {
        content: "";
        display: block;
        position: absolute;
      }

      &:before {
        /* скрыть на md */
        width: 130px;
        height: 122px;
        background: url("../img/choose/1.png") no-repeat center/cover;
        top: 111px;
        left: -186px;

        @include mq(md) {
          display: none;
        }
      }

      &:after {
        width: 92px;
        height: 184px;
        background: url("../img/choose/2.png") no-repeat center/cover;
        bottom: -31px;
        left: -76px;

        @include mq(md) {
          bottom: -9px;
          left: -10px;
        }
      }
    }

