    .price {
      max-width: 300px;
      text-align: center;
      margin-left: 62.54%;

      @include mq(lg) {
        margin-left: 56.54%;
      }

      @include mq(md) {
        margin: 27px auto;
      }
    }

    form {
      max-width: 300px;
      margin: 27px 0 0 62.54%;
      position: relative;

      @include mq(lg) {
        margin-left: 56.54%;
      }

      @include mq(md) {
        margin: 0 auto;
      }

      .form-group {

        input {
          padding: 13px 20px 12px;
        }
      }

      &:before {
        /* скрыть на md */
        content: "";
        display: block;
        width: 162px;
        height: 173px;
        background: url("../img/choose/5.png") no-repeat center/cover;
        position: absolute;
        top: -122px;
        right: -246px;

        @include mq(lg) {
          right: -146px;
        }

        @include mq(md) {
          display: none;
        }
      }
    }

    &:before, &:after {
      content: "";
      display: block;
      position: absolute;
    }

    &:before {
      /* скрыть на md */
      width: 112px;
      height: 112px;
      background: url("../img/choose/3.png") no-repeat center/cover;
      top: -117.04px;
      left: -125.04px;

      @include mq(lg) {
        left: 0;
      }

      @include mq(md) {
        display: none;
      }
    }

    &:after {
      width: 260px;
      height: 178px;
      background: url("../img/choose/4.png") no-repeat center/cover;
      bottom: -117px;
      right: -393px;

      @include mq(lg) {
        right: -153px;
      }

      @include mq(md) {
        width: 130px;
        height: 89px;
        bottom: -89px;
        right: -88px;
      }
    }
